<template>
	<div id="register-box">
		<h1>WatchDog,</h1>
		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="90px" class="demo-ruleForm">
		  <el-form-item label=" :" prop="username" :error="errorMsg1">
		    <el-input type="text" v-model="ruleForm.username" @blur="checkName" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item label=" :" prop="phonenum">
		    <el-input type="text" v-model="ruleForm.phonenum" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item label=" :" prop="capus">
		  		<el-select v-model="ruleForm.capus" placeholder="" class="choice">
		  			<el-option label="" value="tiedao"></el-option>
		  			<el-option label="" value="benbu"></el-option>
		  			<el-option label="" value="nanxiao"></el-option>
		  		</el-select>
		  </el-form-item>
		  <el-form-item label=" :" prop="dorm">
		    <el-input type="text" v-model="ruleForm.dorm" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item label=" :" prop="room">
		    <el-input type="text" v-model="ruleForm.room" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item label=" :" prop="password">
		    <el-input type="password" v-model="ruleForm.password" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item label=" :" prop="checkPass">
		    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" class="input"></el-input>
		  </el-form-item>
		  <el-form-item class="button">
		    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		    <el-button @click="back"></el-button>
		  </el-form-item>
		</el-form>
	</div>
</template>

<script>
import qs from 'qs'
import axios from 'axios'
export default {
	name:'RegisterBox',
     data() {
          var validatePass = (rule, value, callback) => {
            if (value === '') {
              callback(new Error(''));
            } else {
              if (this.ruleForm.checkPass !== '') {
                this.$refs.ruleForm.validateField('checkPass');
              }
              callback();
            }
          };
          var validatePass2 = (rule, value, callback) => {
            if (value === '') {
              callback(new Error(''));
            } else if (value !== this.ruleForm.password) {
              callback(new Error('!'));
            } else {
              callback();
            }
          };
		  var isMobileNumber= (rule, value, callback) => {
		     if (!value) {
		          return new Error("");
		        } else {
		          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
		          const isPhone = reg.test(value);
		          value = Number(value); //
		          if (typeof value === "number" && !isNaN(value)) {//
		          value = value.toString(); //
		            if (value.length < 0 || value.length > 12 || !isPhone) { //11
		              callback(new Error(":138xxxx8754"));
		            } else {
		              callback();
		            }
		          } else {
		            callback(new Error(""));
		          }
		        }
		  };
          return {
            ruleForm: {
			  name:'',
			  username: '',
			  phonenum: '',
			  capus:'',
			  dorm:'',
			  room:'',
              password: '',
              checkPass: '',
            },
            rules: {
			  username: [
			    { required: true, message: '', trigger: 'blur' },
			    // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
			  ],
              password: [
                { validator: validatePass, trigger: 'blur' }
              ],
              checkPass: [
                { validator: validatePass2, trigger: 'blur' }
              ],
			  phonenum: [
			    { required: true, message: '', trigger: 'blur' },
				{ validator: isMobileNumber, trigger: "blur" }
			  ],
			  capus: [
			    { required: true, message: '', trigger: 'change' }
			  ],
			  dorm: [
			    { required: true, message: '', trigger: 'blur' }
			  ],
			  room: [
			    { required: true, message: '', trigger: 'blur' }
			  ],
            },
			info: null,
			errorMsg1:''
          };
        },
        methods: {
		  checkName() {
			  this.errorMsg1 = ""; //
			  var checkUser = this.ruleForm.username;
			  console.log("" + checkUser);

			  let params = new URLSearchParams();
			  params.append('username',checkUser);
			  axios.post('http://47.95.11.87/api/register',params)
					  .then((res)=>{
						  if(res.data==-1){
							  this.errorMsg1 = "";

						  }
					  })
		  },
          submitForm(formName) {
            this.$refs[formName].validate((valid) => {
              if (valid) {
				alert('');
				axios.post('http://127.0.0.1/api/register',
				 qs.stringify({
					 username: this.ruleForm.username,
					 phonenum: this.ruleForm.phonenum,
					 capus: this.ruleForm.capus,
					 dorm: this.ruleForm.dorm,
					 room: this.ruleForm.room,
					 password: this.ruleForm.password}))
					 .then(//res => {
				  //       console.log(res);}
				      response=> (this.info = response)
				      ).catch(res => {
				        console.log(res)
				      })
              } else {
                console.log('');
                return false;
              }
			  console.log(this.ruleForm.username);
            });
          },
          // resetForm(formName) {
          //   this.$refs[formName].resetFields();
          // }
		  back(){
		  	this.$router.push('/login')
		  }
        }
  }
</script>

<style>
#register-box{
	margin: 0 auto; /* 1-4*/
	margin-top: 5%;
	margin-left: 34%;
	text-align: center;
}
#register-box .input{
	width: 25rem;
	padding-left: 0rem;
}
#register-box .choice{
	width: 25rem;
	margin-left: 0.0625rem;
}
#register-box .button{
	width: 33.375rem;
	margin-left: -3rem;
}

#register-box h1{
	text-align: center; 
	margin-bottom: 3.75rem;
	width: 31.25rem;
}
</style>
